<template>
	<view>
		<u-popup v-model="isShow" mode="bottom" @close="closePop">
			<view style="margin-bottom: 26rpx;">
				<view class="card-item-show">
					<view>
						起重机
					</view>
					<view>
						{{showData.crane_name}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						所属机构
					</view>
					<view>
						{{showData.mechanism_name}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						设备类型
					</view>
					<view>
						{{showData.device_type}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						设备型号
					</view>
					<view>
						{{showData.device_model_mumber}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						维修对象
					</view>
					<view>
						{{showData.repair_target}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						报修人
					</view>
					<view>
						{{showData.report_person}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						报修时间
					</view>
					<view>
						{{showData.report_time}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						故障等级
					</view>
					<view>
						{{showData.bug_level}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						故障描述
					</view>
					<view>
						{{showData.bug_description}}
					</view>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	export default {
		name: 'repairDetail',
		props: {
			visible: {
			  type: Boolean,
			  default: false
			},
			showData: {
				type: Object,
				default: function() {
					return {}
				}
			}
		},
		data() {
			return {
				isShow: false
			}
		},
		watch: {
		  visible(val) {
		    this.isShow = val
		  }
		},
		created() {
		},
		methods: {
			closePop() {
				this.isShow = false
				this.$emit('update:visible', false)
				this.$emit('onClose')
			}
		}
	}
</script>

<style lang="scss">
	@charset "UTF-8";
	.card-item-show {
		display: flex;
		padding: 16rpx;
	
		:nth-child(1) {
			width: 25%;
			color: #686868
		}
	
		:nth-child(2) {
			width: 75%;
			text-align: right;
		}
	}
</style>